:root {
  --main-color:  #FF9D00;
  --secondary-color:  #000;
  --emphasize-color: #fff;
}


body {
  font-family: 'Roboto Condensed', sans-serif;
  background: var(--main-color);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  padding: 0;
  margin: 0;
}

.page-container {
  width: 765px;
}
